<script>
import teamBuilding from "@/views/team-building.vue";
export default {
  name: "Home",
  components: {
    teamBuilding
  },
  data() {
    return {
      data: [],
      loading: false,
      busy: false,
      current: 0,
      pokemonName: "",
      pokemonAbility: "",
      pokemonType: "",
      typeList: [],
      selectedPokemon: [],
      pokemonTypeList: [],
      teamBuildingShow: false
    };
  },
  mounted() {
    // this.getAbilityList();
    this.getPokemonList();
    this.getTypesList();
  },
  methods: {
    getPokemonList() {
      if (
        this.pokemonName === "" &&
        this.pokemonAbility === "" &&
        this.pokemonType === ""
      ) {
        let data = {
          limit: 10,
          offset: this.current
        };
        this.$api.common.getPokemonList(data).then(async res => {
          let promiseArr = [];
          res.data.results.forEach(async item => {
            let _data = {
              name: item.name
            };

            let promise = this.$api.common.getPokemonInfo(_data);

            await promiseArr.push(promise);
          });

          await Promise.all(promiseArr).then(values => {
            if (values.length > 0) {
              this.data = this.data.concat(values);
            } else {
              this.$message.warning("所有宝可梦已加载完毕");
              this.current -= 10;
            }

            this.loading = false;
          });
        });
      } else if (this.pokemonType !== "") {
        let promiseArr = [];
        console.log(this.pokemonTypeList);
        this.pokemonTypeList
          .slice(this.current, this.current + 9)
          .forEach(async item => {
            let _data = {
              name: item.pokemon.name
            };

            let promise = this.$api.common.getPokemonInfo(_data);

            await promiseArr.push(promise);
          });

        Promise.all(promiseArr).then(values => {
          if (values.length > 0) {
            this.data = this.data.concat(values);
          } else {
            this.$message.warning("所有宝可梦已加载完毕");
            this.current -= 10;
          }

          this.loading = false;
        });
      } else {
        this.loading = false;
      }
    },
    handleInfiniteOnLoad() {
      this.loading = true;
      this.current += 10;
      this.getPokemonList();
    },
    onSearch() {
      this.loading = true;
      this.current = 0;
      this.data = [];
      if (this.pokemonName !== "") {
        let data = {
          name: this.pokemonName
        };
        this.$api.common
          .getPokemonInfo(data)
          .then(res => {
            this.data.push({
              data: res.data
            });
            this.loading = false;
          })
          .catch(() => {
            this.$message.warning("请输入正确的宝可梦名称(英语)");
          });
      } else {
        this.getPokemonList();
      }
    },
    getAbilityList() {
      let data = {
        offset: 0,
        limit: 293
      };

      this.$api.common.getAbilityList(data).then(res => {
        console.log(res);
      });
    },
    addPokemon(item) {
      this.selectedPokemon.push(item);
    },
    removePokemon(index) {
      this.selectedPokemon.splice(index, 1);
    },
    onSearchAbility() {
      this.loading = true;
      this.current = 0;
      this.data = [];

      if (this.pokemonAbility !== "") {
        let data = {
          name: this.pokemonAbility
        };
        this.$api.common
          .getAbilityInfo(data)
          .then(async res => {
            let promiseArr = [];
            res.data.pokemon.forEach(async item => {
              let _data = {
                name: item.pokemon.name
              };

              let promise = this.$api.common.getPokemonInfo(_data);

              await promiseArr.push(promise);
            });

            await Promise.all(promiseArr).then(values => {
              this.data = values;
              this.loading = false;
            });
          })
          .catch(() => {
            this.$message.warning("请输入正确的特性名称(英语)");
          });
      } else {
        this.getPokemonList();
      }
    },
    getTypesList() {
      let data = {
        offset: 0,
        limit: 100
      };

      this.$api.common.getTypesList(data).then(res => {
        this.typeList = res.data.results;
      });
    },
    onSearchType() {
      this.loading = true;
      this.current = 0;
      this.data = [];

      if (this.pokemonType !== "") {
        let data = {
          name: this.pokemonType
        };

        this.$api.common.getTypesInfo(data).then(async res => {
          let promiseArr = [];
          this.pokemonTypeList = res.data.pokemon;
          res.data.pokemon.slice(0, 9).forEach(async item => {
            let _data = {
              name: item.pokemon.name
            };

            let promise = this.$api.common.getPokemonInfo(_data);

            await promiseArr.push(promise);
          });

          await Promise.all(promiseArr).then(values => {
            this.data = values;
            this.loading = false;
          });
        });
      } else {
        this.getPokemonList();
      }
    },
    toTeamBuilding() {
      this.teamBuildingShow = true;
    }
  }
};
</script>

