<template>
  <a-layout style="padding: 24px 0; background: #fff">
    <a-layout-sider
      width="800"
      style="background: #fff; height: 100%; overflow: hidden"
    >
      <a-card
        v-for="(item, index) in myTeam"
        :key="index"
        style="width: 100%; margin-bottom: 16px"
        class="selection-block"
        :class="selectedPokemon == item.data.name ? 'selection-selected' : ''"
        @click="selectPokemon(item)"
      >
        <div slot="title">
          <a-avatar
            slot="avatar"
            :size="40"
            :src="item.data.sprites.back_default"
          />
          {{ item.data.name }}
          <a-tag
            v-for="typeItem in item.data.types"
            :key="typeItem.slot"
            :color="
              typeItem.type.name === 'fighting'
                ? '#912E1E'
                : typeItem.type.name === 'flying'
                ? '#6699FF'
                : typeItem.type.name === 'poison'
                ? '#AA5599'
                : typeItem.type.name === 'ground'
                ? '#DDBB55'
                : typeItem.type.name === 'rock'
                ? '#BBAA66'
                : typeItem.type.name === 'bug'
                ? '#AABB22'
                : typeItem.type.name === 'ghost'
                ? '#6666BB'
                : typeItem.type.name === 'steel'
                ? '#AAAABB'
                : typeItem.type.name === 'fire'
                ? '#FF4422'
                : typeItem.type.name === 'water'
                ? '#3399FF'
                : typeItem.type.name === 'grass'
                ? '#77CC55'
                : typeItem.type.name === 'electric'
                ? '#FFCC33'
                : typeItem.type.name === 'psychic'
                ? '#FF5599'
                : typeItem.type.name === 'ice'
                ? '#77DDFF'
                : typeItem.type.name === 'dragon'
                ? '#7766EE'
                : typeItem.type.name === 'dark'
                ? '#775544'
                : typeItem.type.name === 'fairy'
                ? '#FFAAFF'
                : ''
            "
          >
            {{ typeItem.type.name }}
          </a-tag>
        </div>
        <div slot="extra">
          <a-tag
            v-for="move in item.data.learnMoves"
            :key="move.data.name"
            :closable="true"
            @close="() => handleClose(move)"
            :color="
              move.data.type.name === 'fighting'
                ? '#912E1E'
                : move.data.type.name === 'flying'
                ? '#6699FF'
                : move.data.type.name === 'poison'
                ? '#AA5599'
                : move.data.type.name === 'ground'
                ? '#DDBB55'
                : move.data.type.name === 'rock'
                ? '#BBAA66'
                : move.data.type.name === 'bug'
                ? '#AABB22'
                : move.data.type.name === 'ghost'
                ? '#6666BB'
                : move.data.type.name === 'steel'
                ? '#AAAABB'
                : move.data.type.name === 'fire'
                ? '#FF4422'
                : move.data.type.name === 'water'
                ? '#3399FF'
                : move.data.type.name === 'grass'
                ? '#77CC55'
                : move.data.type.name === 'electric'
                ? '#FFCC33'
                : move.data.type.name === 'psychic'
                ? '#FF5599'
                : move.data.type.name === 'ice'
                ? '#77DDFF'
                : move.data.type.name === 'dragon'
                ? '#7766EE'
                : move.data.type.name === 'dark'
                ? '#775544'
                : move.data.type.name === 'fairy'
                ? '#FFAAFF'
                : ''
            "
          >
            {{ move.data.names[9].name }}
          </a-tag>
        </div>
      </a-card>
      <a-button
        type="primary"
        block
        v-if="finishButtonShow"
        @click="$message.warning('功能开发中，敬请期待')"
      >
        完成
      </a-button>
    </a-layout-sider>
    <a-layout-content :style="{ padding: '0 24px' }">
      <div style="height: 534px; overflow: auto">
        <a-list item-layout="horizontal" :data-source="selectedPokemonMoves">
          <a-list-item slot="renderItem" slot-scope="item">
            <a-list-item-meta>
              <span slot="title">
                {{ item.data.names[9].name }}
                <a-tag
                  :color="
                    item.data.type.name === 'fighting'
                      ? '#912E1E'
                      : item.data.type.name === 'flying'
                      ? '#6699FF'
                      : item.data.type.name === 'poison'
                      ? '#AA5599'
                      : item.data.type.name === 'ground'
                      ? '#DDBB55'
                      : item.data.type.name === 'rock'
                      ? '#BBAA66'
                      : item.data.type.name === 'bug'
                      ? '#AABB22'
                      : item.data.type.name === 'ghost'
                      ? '#6666BB'
                      : item.data.type.name === 'steel'
                      ? '#AAAABB'
                      : item.data.type.name === 'fire'
                      ? '#FF4422'
                      : item.data.type.name === 'water'
                      ? '#3399FF'
                      : item.data.type.name === 'grass'
                      ? '#77CC55'
                      : item.data.type.name === 'electric'
                      ? '#FFCC33'
                      : item.data.type.name === 'psychic'
                      ? '#FF5599'
                      : item.data.type.name === 'ice'
                      ? '#77DDFF'
                      : item.data.type.name === 'dragon'
                      ? '#7766EE'
                      : item.data.type.name === 'dark'
                      ? '#775544'
                      : item.data.type.name === 'fairy'
                      ? '#FFAAFF'
                      : ''
                  "
                >
                  {{ item.data.type.name }}
                </a-tag>
              </span>
              <div slot="description">
                {{
                  item.data.flavor_text_entries.filter(textItem => {
                    return textItem.language.name == "zh-Hans";
                  })[0].flavor_text
                }}
              </div>
            </a-list-item-meta>
            <div slot="extra">
              <a-button
                type="primary"
                shape="circle"
                icon="plus"
                style="line-height: 1"
                @click="addMoveToPokemon(item)"
              />
            </div>
          </a-list-item>
        </a-list>
      </div>
    </a-layout-content>
  </a-layout>
</template>

<script>
export default {
  props: {
    team: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      selectedPokemon: "",
      selectedPokemonMoves: [],
      finishButtonShow: false
    };
  },
  computed: {
    myTeam: {
      get() {
        this.team.forEach(item => {
          item.data.learnMoves = [];
        });
        return this.team;
      },
      set() {}
    },
    selectedPokemonLearnedMoves() {
      return this.myTeam.filter(pokemon => {
        return pokemon.data.name == this.selectedPokemon;
      })[0].data.learnMoves;
    }
  },
  methods: {
    selectPokemon(item) {
      this.selectedPokemon = item.data.name;

      let data = {
        name: this.selectedPokemon
      };

      this.$api.common.getPokemonInfo(data).then(async res => {
        let promiseArr = [];
        res.data.moves.forEach(async item => {
          let _data = {
            name: item.move.name
          };

          let promise = this.$api.common.getMoveInfo(_data);

          await promiseArr.push(promise);
        });

        await Promise.all(promiseArr).then(values => {
          this.selectedPokemonMoves = values;
        });
      });
    },
    addMoveToPokemon(item) {
      if (this.selectedPokemonLearnedMoves.length < 4) {
        this.myTeam
          .filter(pokemon => {
            return pokemon.data.name == this.selectedPokemon;
          })[0]
          .data.learnMoves.push(item);
      } else {
        this.$message.warning("已选择四项技能");
      }

      let movesAmount = 0;
      this.myTeam.forEach(item => {
        movesAmount += item.data.learnMoves.length;
      });
      if (movesAmount == 24) {
        this.finishButtonShow = true;
      } else {
        this.finishButtonShow = false;
      }

      this.$forceUpdate();
    },
    handleClose(move) {
      for (let i in this.selectedPokemonLearnedMoves) {
        if (this.selectedPokemonLearnedMoves[i].data.name === move.data.name) {
          this.selectedPokemonLearnedMoves.splice(i, 1);
        }
      }

      let movesAmount = 0;
      this.myTeam.forEach(item => {
        movesAmount += item.data.learnMoves.length;
      });
      if (movesAmount == 24) {
        this.finishButtonShow = true;
      } else {
        this.finishButtonShow = false;
      }
    }
  }
};
</script>

<style lang="less" scoped>
.selection-block {
  cursor: pointer;

  /deep/.ant-card-head {
    border-bottom: 0;
  }

  &&:hover {
    border: 1px solid blue;
  }
}

.selection-selected {
  border: 1px solid blue;
}
</style>
